import {Component, Inject, OnInit} from "@angular/core";
import {Contact, Task} from "../../../../../shared/interfaces";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {Router} from "@angular/router";

@Component({
  selector: 'app-task-contacts',
  templateUrl: './task-contacts.component.html',
  styleUrls: ['./task-contacts.component.css']
})
export class TaskContactsComponent implements OnInit {
  task: Task;

  constructor(
    private router: Router,
    private thisDialog: MatDialogRef<TaskContactsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: object) {
  }

  ngOnInit(): void {
    this.task = this.data["task"];
  }

  goto(contact: Contact) {
    this.router.navigate(['/profile', 'contacts'], {
      queryParams: {
        select: contact.id
      }
    });
    this.thisDialog.close();
  }
}
