import {Component, Input, OnDestroy} from '@angular/core';
import {Subscription} from 'rxjs';
import {Router} from '@angular/router';

import {Task} from '../../shared/interfaces';
import {TasksService} from '../shared/services/tasks.service';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnDestroy {

  @Input() private enable: boolean = false;
  private subscription: Subscription;
  taskNotifications: Task[] = [];

  constructor(
    public tasksService: TasksService,
    private router: Router
  ) {
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  public toggleEnableNotifications() {
    this.enable = !this.enable;

    if (this.enable) {
      this.subscription = this.tasksService.check$
        .subscribe((tasks: Task[]) => {
          console.log('Notification tick');
          if (tasks.length) {
            for (let task of tasks) {
              this.taskNotifications.push(task);
            }
          }
        });
    }

    if (!this.enable) {
      this.subscription.unsubscribe();
    }
  }

  onGoto(notification: Task) {
    this.router.navigate(['/profile', 'tasks'], {
      queryParams: {
        select: notification.id
      }
    });
    this.onHide(notification);
  }

  onHide(notification: Task) {
    this.taskNotifications = this.taskNotifications.filter(task => task.id !== notification.id);
  }

}
