import {Directive, ElementRef, HostListener, Renderer2} from "@angular/core";

@Directive({
  selector: "[textAreaAutoResize]"
})
export class TextAreaAutoResize {

  constructor(
    private element: ElementRef,
    private renderer: Renderer2
  ) {
    this.renderer.setStyle(this.element.nativeElement, 'resize', 'none');
  }

  @HostListener('keyup') onKeyup() {
    this.renderer.setStyle(this.element.nativeElement, 'overflow', 'hidden');
    this.renderer.setStyle(this.element.nativeElement, 'height', '0');

    let height = +this.element.nativeElement.style.minHeight + +this.element.nativeElement.scrollHeight;

    this.renderer.setStyle(
      this.element.nativeElement,
      'height',
      height + 'px'
    );
  }
}
