import {Injectable} from '@angular/core';
import {environment} from '../../../../environments/environment';
import {map, tap} from 'rxjs/operators';

import {Contact} from '../../../shared/interfaces';
import {HttpService} from '../../../shared/services';
import {XmlBuilderService} from './xml-builder.service';
import {FileManagerService} from './file-manager.service';
import {SnackbarService} from '../../../shared/services/snackbar.service';

@Injectable()
export class ContactsService {

  private readonly REQUEST_URL = `${environment.serverUrl}/ContactActionServlet`;
  contacts: Contact[];

  constructor(
    private http: HttpService,
    private xmlBuilder: XmlBuilderService,
    private fileManager: FileManagerService,
    private snackbarService: SnackbarService,
  ) {
  }

  getAll() {
    return this.http.get(this.REQUEST_URL, {})
      .pipe(
        map(response => this.xmlBuilder.xmlToContacts(response)),
        tap((contacts: Contact[]) => this.contacts = contacts)
      )
  }

  add(newContact: Contact) {
    const params = {
      action: 'add',
      xml: this.xmlBuilder.contactToXml(newContact)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe((generatedId) => {
        newContact.id = +generatedId;
        this.contacts.push(newContact);
        this.snackbarService.openSnackBar("Contact added");
      });
  }

  update(contact: Contact) {
    const params = {
      action: 'update',
      xml: this.xmlBuilder.contactToXml(contact)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe(() => {
        let index = this.contacts.findIndex(ct => ct.id === contact.id);
        this.contacts[index] = contact;
        this.snackbarService.openSnackBar("Contact edited");
      });
  }

  delete(contact: Contact) {
    const params = {
      action: 'delete',
      xml: this.xmlBuilder.contactToXml(contact)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe(() => {
        this.contacts = this.contacts.filter(ct => ct.id !== contact.id);
        this.snackbarService.openSnackBar("Contact deleted");
      });
  }

  import() {
    this.fileManager.upload();
    let sub = this.fileManager.uploadResult.subscribe(result => {

      const params = {
        action: 'import',
        xml: result
      };

      this.http.post(this.REQUEST_URL, params)
        .subscribe(() => {
            this.getAll().subscribe();
            this.snackbarService.openSnackBar("Contacts import success")
          },
          () => this.snackbarService.openSnackBar("Contacts import fail"),
          () => sub.unsubscribe());
    });
  }

  export(contacts?: Contact[]): void {
    this.fileManager.unload(
      this.xmlBuilder.contactsToXml(
        contacts && contacts.length ? contacts : this.contacts),
      'contacts-export.xml'
    );
  }

  search(value, categories?: string[]) {
    const params = {
      value: value,
    };

    if (categories.length) {
      for (let category of categories) {
        params[category] = true;
      }
    }

    return this.http.get(this.REQUEST_URL, params)
      .pipe(
        map(response => this.xmlBuilder.xmlToContacts(response))
      )
  }

}
