import {AfterViewInit, Component, ElementRef, Input, OnDestroy, ViewChild} from "@angular/core";

declare let Isotope: any;

@Component({
  selector: "app-isotope-container",
  templateUrl: "./isotope-container.component.html",
  styleUrls: ["./isotope-container.component.css"]
})
export class IsotopeContainerComponent implements AfterViewInit, OnDestroy {

  @Input() selector: string;
  @Input() title: string;
  @ViewChild("container") container: ElementRef;

  private isotope;
  private mutationsObserver: MutationObserver;

  constructor() {

  }

  ngAfterViewInit(): void {
    if (this.container) {
      this.isotope = new Isotope(this.container.nativeElement, {
        itemSelector: this.selector,
        layoutMode: 'masonry',
      });

      this.mutationsObserver = new MutationObserver((mutations: MutationRecord[]) => {
        console.log(mutations);
        for (let mutation of mutations) {

          if (mutation.addedNodes.length) {
            for (let i = 0; i < mutation.addedNodes.length; i++) {
              this.isotope.prepended(mutation.addedNodes.item(i));
            }
          }

          if (mutation.removedNodes.length) {
            for (let i = 0; i < mutation.removedNodes.length; i++) {
              this.isotope.remove(mutation.removedNodes.item(i));
            }
          }

          this.isotope.layout();
        }
      });

      this.mutationsObserver.observe(this.container.nativeElement, {
        childList: true,
        attributeFilter: ['class']
      })
    }
  }

  ngOnDestroy(): void {
    if (this.mutationsObserver) {
      this.isotope.destroy();
      this.mutationsObserver.disconnect();
    }
  }

}
