import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {TaskActionComponent} from './shared/components/task-action/task-action.component';
import {TasksPageComponent} from './tasks-page/tasks-page.component';
import {RouterModule} from '@angular/router';
import {FixedTasksPipe} from "./pipes/fixed-tasks.pipe";
import {TasksSharedModule} from "./shared/tasks-shared.module";
import {TaskContactsComponent} from "./shared/components/task-contacts/task-contacts.component";
import {DialogOverviewService} from "../shared/services/dialog-overview.service";

@NgModule({
  declarations: [
    TasksPageComponent,
    FixedTasksPipe,
  ],
  imports: [
    CommonModule,
    TasksSharedModule,

    RouterModule.forChild([
      {path: '', component: TasksPageComponent},
    ]),
  ],

  entryComponents: [
    TaskActionComponent,
    TaskContactsComponent
  ],

  providers: [
    DialogOverviewService
  ]

})
export class TasksModule {

}
