import {Contact, Task} from '../../../shared/interfaces';
import {Injectable} from '@angular/core';
import {DatePipe} from '@angular/common';

@Injectable()
export class XmlBuilderService {

  constructor(private _datePipe: DatePipe) {
  }

  tasksToXml(tasks: Task[]): string {
    let tasksElement = document.createElement("tasks");
    for (let task of tasks) {
      tasksElement.appendChild(this.createTaskElement(task));
    }
    return tasksElement.outerHTML;
  }

  contactsToXml(contacts: Contact[]): string {
    let contactsElement = document.createElement("contacts");
    for (let contact of contacts) {
      contactsElement.appendChild(this.createContactElement(contact));
    }
    return contactsElement.outerHTML;
  }

  taskToXml(task: Task): string {
    return this.createTaskElement(task).outerHTML;
  }

  contactToXml(contact: Contact): string {
    return this.createContactElement(contact).outerHTML;
  }

  xmlToTasks(xml: string | Element): Task[] {
    let root;

    if (typeof xml === 'string') {
      let parser: DOMParser = new DOMParser();
      root = parser.parseFromString(xml, 'text/xml');
    } else {
      root = xml;
    }

    let taskElements = root.querySelector('tasks');
    let tasks: Task[] = [];

    for (let i = 0; i < taskElements.childNodes.length; i++) {
      let taskElement = taskElements.childNodes.item(i) as Element;

      let id = taskElement.querySelector('id').innerHTML;
      let name = taskElement.querySelector('name').innerHTML;
      let description = taskElement.querySelector('description').innerHTML;
      let completed = taskElement.querySelector('completed').innerHTML;
      let fixed = taskElement.querySelector('fixed').innerHTML;
      console.log(fixed);

      let notifyDatetime = taskElement.querySelector('notify-timestamp').innerHTML;

      let contacts: Contact[] = this.xmlToContacts(taskElement, 'task-contacts');

      tasks.push({
        id: +id,
        name,
        description,
        completed: (completed === "true"),
        fixed: (fixed === "true"),
        notifyTimestamp: new Date(notifyDatetime),
        contacts
      });
    }

    return tasks;
  }

  xmlToContacts(xml: string | Element, tag?): Contact[] {
    let root;

    if (typeof xml === 'string') {
      let parser: DOMParser = new DOMParser();
      root = parser.parseFromString(xml, 'text/xml');
    } else {
      root = xml;
    }

    let contacts: Contact[] = [];
    let contactElements = root.querySelector(tag ? tag : 'contacts');

    for (let i = 0; i < contactElements.childNodes.length; i++) {
      let contactElement = contactElements.childNodes.item(i) as Element;

      let id = contactElement.querySelector('id').innerHTML;
      let name = contactElement.querySelector('name').innerHTML;
      let phone_number = contactElement.querySelector('phone-number').innerHTML;
      contacts.push({id: +id, name, phone_number});
    }

    return contacts;
  }

  private createTaskElement(task: Task): Element {
    let taskElement = document.createElement('task');

    if (task.id) {
      taskElement.appendChild(this.createElement('id', task.id));
    }

    taskElement.appendChild(this.createElement('name', task.name));
    taskElement.appendChild(this.createElement('description', task.description));
    taskElement.appendChild(this.createElement('completed', task.completed));
    taskElement.appendChild(this.createElement('fixed', task.fixed));

    let notifyDatetime: string = this._datePipe.transform(task.notifyTimestamp, "yyyy-MM-dd HH:mm");
    taskElement.appendChild(this.createElement('notify-timestamp', notifyDatetime));

    if (task.contacts) {
      let contacts = document.createElement('task-contacts');
      for (let contact of task.contacts) {
        contacts.appendChild(this.createContactElement(contact));
      }
      taskElement.appendChild(contacts);
    }

    return taskElement;
  }

  private createContactElement(contact: Contact): Element {
    let contactElement = document.createElement('contact');

    if (contact.id) {
      contactElement.appendChild(this.createElement('id', contact.id));
    }

    contactElement.appendChild(this.createElement('name', contact.name));
    contactElement.appendChild(this.createElement('phone-number', contact.phone_number));

    return contactElement;
  }

  private createElement(elementName, content): Element {
    let element = document.createElement(elementName);
    element.innerText = content;
    return element;
  }
}
