import {Injectable} from '@angular/core';
import {environment} from '../../../../environments/environment';
import {map, switchMap, tap} from 'rxjs/operators';
import {interval, Subject, Subscription} from 'rxjs';

import {Task} from '../../../shared/interfaces';
import {FileManagerService} from './file-manager.service';
import {HttpService} from '../../../shared/services';
import {XmlBuilderService} from './xml-builder.service';
import {SnackbarService} from '../../../shared/services/snackbar.service';
import {ContactsService} from "./contacts.service";


@Injectable()
export class TasksService {

  private readonly REQUEST_URL = `${environment.serverUrl}/TaskActionServlet`;

  tasks: Task[];
  check$: Subject<Task[]>;
  private checkSubscription: Subscription;

  constructor(
    private http: HttpService,
    private fileManager: FileManagerService,
    private contactsService: ContactsService,
    private xmlBuilder: XmlBuilderService,
    private snackbarService: SnackbarService
  ) {
  }

  getAll() {
    return this.http.get(this.REQUEST_URL, {})
      .pipe(
        map(response => this.xmlBuilder.xmlToTasks(response)),
        tap((tasks: Task[]) => this.tasks = tasks),
      )
  }

  startChecking() {
    this.check$ = new Subject<Task[]>();
    this.checkSubscription = interval(10000)
      .pipe(
        switchMap(() =>
          this.http.post(this.REQUEST_URL, {action: 'check'})
            .pipe(
              map(response => this.xmlBuilder.xmlToTasks(response))
            )
        )
      )
      .subscribe((tasks: Task[]) => {
        for (let task of tasks) {
          let index = this.tasks.findIndex(tsk => tsk.id === task.id);
          this.tasks[index] = task;
        }
        this.check$.next(tasks);
      });
  }

  stopChecking() {
    if (this.checkSubscription && this.check$) {
      this.checkSubscription.unsubscribe();
      this.check$.complete();
    }
  }

  add(newTask: Task) {
    const params = {
      action: 'add',
      xml: this.xmlBuilder.taskToXml(newTask)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe((generatedId) => {
        newTask.id = +generatedId;
        this.tasks.push(newTask);
        this.snackbarService.openSnackBar("Task added");
      });
  }

  update(task: Task) {
    const params = {
      action: 'update',
      xml: this.xmlBuilder.taskToXml(task)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe(() => {
        let index = this.tasks.findIndex(tsk => tsk.id === task.id);
        this.tasks[index] = task;
        this.snackbarService.openSnackBar("Task edited");
      });
  }

  delete(task: Task) {
    const params = {
      action: 'delete',
      xml: this.xmlBuilder.taskToXml(task)
    };
    this.http.post(this.REQUEST_URL, params)
      .subscribe(() => {
        this.tasks = this.tasks.filter(tsk => tsk.id !== task.id);
        this.snackbarService.openSnackBar("Task deleted");
      });
  }

  search(value, categories?: string[]) {
    const params = {
      value,
    };

    if (categories.length) {
      for (let category of categories) {
        params[category] = true;
      }
    }

    return this.http.get(this.REQUEST_URL, params)
      .pipe(
        map(response => this.xmlBuilder.xmlToTasks(response))
      )
  }

  import() {
    this.fileManager.upload();
    let sub = this.fileManager.uploadResult.subscribe(result => {

      const params = {
        action: 'import',
        xml: result
      };

      this.http.post(this.REQUEST_URL, params)
        .subscribe(() => {
            this.contactsService.getAll()
              .subscribe(
                () => this.getAll().subscribe()
              );
            this.snackbarService.openSnackBar("Tasks import success");
          },
          () => this.snackbarService.openSnackBar("Tasks import fail"),
          () => sub.unsubscribe());
    });
  }

  export(tasks?: Task[]) {
    this.fileManager.unload(
      this.xmlBuilder.tasksToXml(tasks && tasks.length ? tasks : this.tasks),
      'tasks-export.xml'
    );
  }


}
