import {AfterViewInit, Component, ElementRef, Input} from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';

import {Task} from '../../../../../shared/interfaces';
import {TasksService} from '../../../../shared/services/tasks.service';
import {DialogOverviewService} from "../../../../shared/services/dialog-overview.service";
import {TaskActionComponent} from "../task-action/task-action.component";
import {TaskContactsComponent} from "../task-contacts/task-contacts.component";

@Component({
  selector: 'app-task-card',
  templateUrl: './task-card.component.html',
  styleUrls: ['./task-card.component.css']
})

export class TaskCardComponent implements AfterViewInit {

  @Input() task: Task;

  isSelected;

  constructor(
    private tasksService: TasksService,
    private route: ActivatedRoute,
    private dialogOverview: DialogOverviewService,
    private el: ElementRef
  ) {
  }

  ngAfterViewInit(): void {
    this.route.queryParams.subscribe((params: Params) => {
      if (+params["select"] === this.task.id) {
        this.isSelected = true;
        this.el.nativeElement.scrollIntoView({behavior: 'smooth'});
      }
    })
  }

  onCopy() {
    let clone: Task = Object.assign({}, this.task);
    clone.id = null;
    this.tasksService.add(clone);
  }

  onDelete() {
    this.tasksService.delete(this.task);
  }

  onEdit() {
    this.dialogOverview.openDialog(TaskActionComponent, {
      action: 'edit',
      task: {...this.task}
    });
  }

  Fix() {
    let clone: Task = Object.assign(this.task);
    clone.fixed = !clone.fixed;
    this.tasksService.update(clone);
  }

  openContacts() {
    this.dialogOverview.openDialog(TaskContactsComponent, {
      task: {...this.task}
    });
  }

  hideSelect() {
    this.isSelected = false;
  }
}
