import {NgModule} from '@angular/core';
import {CommonModule, DatePipe} from '@angular/common';
import {RouterModule} from '@angular/router';

import {ProfileComponent} from './profile.component';
import {NotificationComponent} from './notification/notification.component';
import {ContactsService} from './shared/services/contacts.service';
import {TasksService} from './shared/services/tasks.service';
import {ProfileSharedModule} from './shared/profile-shared.module';
import {FileManagerService} from './shared/services/file-manager.service';
import {XmlBuilderService} from './shared/services/xml-builder.service';
import {SnackbarService} from '../shared/services/snackbar.service';
import {SharedModule} from '../shared/shared.module';
import {TasksModule} from "./tasks/tasks.module";

@NgModule({
  declarations: [
    ProfileComponent,
    NotificationComponent,
  ],
  imports: [
    CommonModule,
    ProfileSharedModule,
    SharedModule,

    RouterModule.forChild([
      {
        path: '', component: ProfileComponent, children: [
          {path: '', redirectTo: '/profile/tasks', pathMatch: 'full'},
          {path: 'tasks', loadChildren: () => import('./tasks/tasks.module').then(m => m.TasksModule)},
          {
            path: 'remainders',
            loadChildren: () => import('./remainders/remainders.module').then(m => m.RemaindersModule)
          },
          {path: 'contacts', loadChildren: () => import('./contacts/contacts.module').then(m => m.ContactsModule)}
        ]
      }
    ]),
    TasksModule,
  ],
  providers: [
    ContactsService,
    TasksService,

    FileManagerService,
    SnackbarService,

    XmlBuilderService,

    DatePipe,
  ]
})

// SearchService,

export class ProfileModule {
  constructor() {
  }
}
