import {Component, EventEmitter, Input, Output} from "@angular/core";

export interface SearchResult {
  value: string,
  subSelectors: SelectorOption[];
}

export interface SelectorOption {
  name: string,
  selector: string,
  isActive: boolean
}

export interface SearchOptions {
  selector: string;
  subSelectors: SelectorOption[];
  uniqueAttribute: string;
}

@Component({
  selector: 'app-search',
  templateUrl: "./search.component.html",
  styleUrls: ["./search.component.css"],
})
export class SearchComponent {

  @Output() onInputEvent: EventEmitter<SearchResult> = new EventEmitter<SearchResult>();
  @Input() searchOptions: SearchOptions;
  private inputValue;

  constructor() {
  }

  activeSelector(category: SelectorOption) {
    category.isActive = true;
  }

  deactivateSelector(category: SelectorOption) {
    category.isActive = false;
  }

  isActiveSelector(category: SelectorOption): boolean {
    return category.isActive;
  }

  onInput(value) {
    this.inputValue = value;
    let searchResult: SearchResult = {
      value,
      subSelectors: this.searchOptions.subSelectors.filter(
        selector => selector.isActive
      )
    };
    this.onInputEvent.emit(searchResult);
  }

  public searchAndMark() {
    this.clearAll();

    //   -
    let activeSubSelectors: SelectorOption[] = this.searchOptions.subSelectors.filter(ctg => ctg.isActive);

    //   - ,   
    let curSelectors: SelectorOption[] =
      activeSubSelectors.length ? activeSubSelectors : this.searchOptions.subSelectors;

    //    
    let elements = document.querySelectorAll(this.searchOptions.selector);

    for (let i = 0; i < elements.length; i++) {
      let element = elements.item(i) as HTMLElement;
      this.markBySelectors(this.inputValue, element, curSelectors);
    }
  }

  private markBySelectors(value, element, selectors) {
    for (let selector of selectors) {
      //     -
      let child = element.querySelector(selector.selector) as HTMLElement;
      //   
      if (child) {
        let startMarkIndex = child.textContent.search(value);
        if (startMarkIndex !== -1) {
          this.mark(child, startMarkIndex, value.length);
          child.classList.add('searcher-mark');
        }
      }

    }
  }

  private clearAll(): void {
    let elements = document.querySelectorAll(this.searchOptions.selector);

    for (let i = 0; i < elements.length; i++) {
      let markedElements = elements.item(i).querySelectorAll('.searcher-mark');

      for (let j = 0; j < markedElements.length; j++) {
        markedElements.item(j).innerHTML = markedElements.item(j).textContent;
        markedElements.item(j).classList.remove('searcher-mark');
      }
    }
  }

  private mark(element, start: number, end: number): void {
    element.innerHTML =
      element.textContent.substring(0, start) +
      '<mark>' +
      element.textContent.substring(start, start + end) +
      '</mark>' +
      element.textContent.substring(start + end);
  }
}
