import {Component, OnInit} from '@angular/core';
import {TasksService} from '../../shared/services/tasks.service';
import {Task} from 'src/app/shared/interfaces';
import {SearchOptions} from "../../shared/components/search/search.component";
import {TaskActionComponent} from "../shared/components/task-action/task-action.component";
import {DialogOverviewService} from "../../shared/services/dialog-overview.service";

@Component({
  selector: 'app-tasks-page',
  templateUrl: './tasks-page.component.html',
  styleUrls: ['./tasks-page.component.css'],
})
export class TasksPageComponent implements OnInit {

  searchOptions: SearchOptions;
  searchedTasks: Task[] = [];
  noSearchedTasks: boolean = true;
  searchNow = false;

  constructor(
    public tasksService: TasksService,
    private dialogOverview: DialogOverviewService
  ) {
  }

  ngOnInit(): void {
    this.searchOptions = {
      uniqueAttribute: 'id',
      selector: '.task',
      subSelectors: [
        {name: 'name', selector: '.name', isActive: false},
        {name: 'description', selector: '.description', isActive: false},
        {name: 'time', selector: '.notify-timestamp', isActive: false},
      ]
    };
  }

  onSearch(event) {
    if (event["value"] === '') {
      this.searchedTasks = [];
      this.noSearchedTasks = true;
      this.searchNow = false;
      return;
    }
    this.tasksService.search(event["value"], event["subSelectors"])
      .subscribe((tasks: Task[]) => {
        this.searchedTasks = tasks;
        this.noSearchedTasks = !this.searchedTasks.length;
        this.searchNow = true;
      });
  }

  onExport() {
    if (this.searchedTasks.length) {
      this.tasksService.export(this.searchedTasks);
    } else {
      this.tasksService.export();
    }
  }

  onImport() {
    this.tasksService.import();
  }

  onAddTask() {
    this.dialogOverview.openDialog(TaskActionComponent, {
      action: 'create',
    });
  }
}
