import {Pipe, PipeTransform} from '@angular/core';
import {Task} from '../../../shared/interfaces';

@Pipe({
  name: 'fixedTasks',
  pure: false
})
export class FixedTasksPipe implements PipeTransform {

  transform(tasks: Task[], filter: 'fixed' | 'unfixed' | string): Task[] {
    if (!tasks) return [];
    if (tasks && filter === 'fixed') {
      return tasks.filter((task: Task) => task.fixed);
    } else if (tasks && filter === 'unfixed') {
      return tasks.filter((task: Task) => !task.fixed);
    }
  }
}
